import { defineStore } from 'pinia';
import Vue from 'vue';
export const useDragAndDropStore = defineStore('dragging', {
    state: () => ({
        dragging: [],
    }),
    actions: {
        /**
         * Set the selection of fileIds
         */
        set(selection = []) {
            Vue.set(this, 'dragging', selection);
        },
        /**
         * Reset the selection
         */
        reset() {
            Vue.set(this, 'dragging', []);
        },
    },
});
